//reset
*{
   margin: 0; padding: 0;
 }

html, body {
   width: 100%;
   height: 100%;
 }

 header, nav, footer{
  width: 100%;  
}

 header, nav{
   height: 10%;
 }

 @mixin textCenter{
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bolder;  
 }

 header{
  text-align: center;
 }

 footer{
   height: 5%;
   font-size: 1em;
   @include textCenter;
 }

 #mainContainer{
   width: 90%;
   height: 75%;
   margin: 0 auto;
   font-size: 1em;
   @include textCenter;
 }

body{
  background-color: 	#A9A9A9;
}

